<template>
  <div>
    <v-dialog v-model="showDialog" persistent max-width="400">
      <v-card :loading="isRequest" :disabled="isRequest">
        <v-card-title>
          <span class="text-h5">Eliminar cartelera</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" @click="del()"> Eliminar </v-btn>
          <v-btn color="primary" @click="toggleDialog"> Cancelar </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from "../../services/axios";
export default {
  data: () => {
    return {
      showDialog: true,
      isRequest: false,
    };
  },
  methods: {
    del() {
      this.isRequest = true;
      axios.delete("/api/carteleras/" + this.item["_id"]).then((res) => {
        this.isRequest = false;
        this.refresh();
        this.toggleDialog();
      });
    },
  },
  props: ["item", "toggleDialog", "refresh"],
};
</script>