<template>
  <v-app>
    <v-main>
      <h1 class="text-center">Cartelera</h1>
      <v-container>
        <v-row
          justify="center"
          v-for="(item, index) in listFechas"
          :key="index"
        >
          <v-col cols="12" sm="5">
            <v-card elevation="8" class="rounded-lg mx-auto">
              <v-card-text>
                <v-container fill-height fluid>
                  <v-row justify="center">
                    <p>{{ item["fechas"] }}</p>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-divider></v-divider>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn fab small color="accent">
                  <v-icon color="white">mdi-pencil</v-icon>
                </v-btn>
                <v-btn fab small color="red" @click="btnDelete(item)">
                  <v-icon color="white">mdi-delete</v-icon>
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
    <DialogAdd :refreshCarteleras="getCarteleras"></DialogAdd>
    <DialogDelete
      v-if="isDialogDelete"
      :toggleDialog="toggleDialogDelete"
      :item="itemSelected"
      :refresh="getCarteleras"
    ></DialogDelete>
  </v-app>
</template>

<script>
import axios from "../services/axios";
import DialogAdd from "../components/Carteleras/DialogAdd";
import DialogDelete from "../components/Carteleras/DialogDelete";

export default {
  data: () => {
    return {
      listFechas: [],
      isDialogDelete: false,
      itemSelected: null,
    };
  },
  methods: {
    getCarteleras() {
      this.listFechas = [];
      axios.get("/api/carteleras/all").then((res) => {
        this.listFechas = res.data;
      });
    },
    btnDelete(item) {
      this.itemSelected = item;
      this.toggleDialogDelete();
    },
    toggleDialogDelete() {
      this.isDialogDelete = !this.isDialogDelete;
    },
  },
  components: {
    DialogAdd,
    DialogDelete,
  },
  created() {
    this.getCarteleras();
  },
};
</script>

<style lang="scss" scoped>
.v-btn.add {
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.container-f {
  height: 85vh;
  overflow-y: auto;
  overflow-x: hidden;
}
</style>