<template>
  <div>
    <v-dialog v-model="showDialog" persistent max-width="700">
      <v-card :loading="isRequest" :disabled="isRequest">
        <v-card-title>
          <span class="text-h5">Nueva cartelera</span>
        </v-card-title>
        <v-divider></v-divider>

        <v-card-text>
          <v-container fill-height fluid>
            <v-row justify="center">
              <v-date-picker
                color="blue"
                v-model="date"
                multiple
                class="mt-5"
                elevation="5"
              ></v-date-picker>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="toggleDialog()">
            Cerrar
          </v-btn>
          <v-btn color="blue darken-1" text @click="save()"> Guardar </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-btn
      fab
      dark
      small
      color="blue"
      fixed
      right
      bottom
      @click="toggleDialog()"
    >
      <v-icon>mdi-plus</v-icon>
    </v-btn>
  </div>
</template>

<script>
import axios from "../../services/axios";

export default {
  data: () => {
    return {
      showDialog: false,
      isRequest: false,
      date: [],
    };
  },
  methods: {
    toggleDialog() {
      this.showDialog = !this.showDialog;
    },
    save() {
      let d = new Date(this.date[0]);
      if (this.date) {
        this.isRequest = true;
        axios
          .post("/api/carteleras/", {
            mes: d.toLocaleString("default", { month: "long" }),
            fechas: this.date,
          })
          .then((res) => {
            this.isRequest = false;
            if (res.data["status"] == 200) {
              this.refreshCarteleras();
              this.toggleDialog();
            }
          });
      }
    },
  },
  props: ["refreshCarteleras"],
};
</script>